.pdf {
  width: 780px;
  height: 100%;
  color: var(--color-dark);
  font-size: 14px;
  line-height: 20px;
  margin: 0 auto;
}

.pdf h2 {
  font-size: 24px;
  line-height: 28px;
}

.pdf h3 {
  font-size: 18px;
  line-height: 22px;
  margin-bottom: 0;
}

.pdf h4 {
  font-size: 16px;
  line-height: 24px;
  margin-bottom: 0;
  font-weight: 700;
}

.pdf b {
  font-weight: 700;
}

.pdf-main .pdf-section__left, .pdf-planoplan .pdf-section__left {
  justify-content: center;
}

.pdf__container {
  padding-left: 8.3333333333%;
  padding-right: 8.3333333333%;
  padding-top: 32px;
  padding-bottom: 32px;
  width: 100%;
  min-height: 1100px;
  break-before: page;
  break-inside: avoid;
  display: flex;
  flex-direction: column;
}

.pdf-header {
  position: relative;
  display: flex;
  align-items: flex-start;
  padding-bottom: 16px;
}

.pdf-header:before {
  position: absolute;
  bottom: 0;
  width: 100%;
  border-top: 1px solid rgba(var(--color-black-rgb), 0.1);
  content: "";
}

.pdf-header__logo {
  margin-right: 16.6666666667%;
  width: 20%;
}

.pdf-header__logo img {
  max-height: 60px;
}

.pdf-header__contacts {
  display: flex;
  justify-content: space-between;
  width: 63.3333333333%;
}

.pdf-header__contacts-item {
  display: flex;
  flex-direction: column;
  font-size: 12px;
  line-height: 18px;
  font-weight: 400;
}

.pdf-header__contacts-type {
  color: rgba(var(--color-black-rgb), 0.5);
}

.pdf-footer {
  position: relative;
  align-self: flex-end;
  margin-top: auto;
  padding-top: 32px;
  width: 100%;
}

.pdf-footer:before {
  content: "";
  display: block;
  height: 16px;
  border-top: 1px solid rgba(var(--color-black-rgb), 0.1);
}

.pdf-footer__items {
  display: flex;
  justify-content: space-between;
}

.pdf-footer__item {
  font-size: 10px;
  line-height: 15px;
  font-weight: 400;
  color: rgba(var(--color-black-rgb), 0.5);
}

.pdf__text {
  font-size: 14px;
  line-height: 21px;
}

.pdf-section {
  display: flex;
  margin-top: 32px;
}

.pdf-section__left {
  margin-right: 13.3333333333%;
  display: flex;
  width: 43.3333333333%;
}

.pdf-section__right {
  width: 43.3333333333%;
}

.pdf-main__heading {
  margin-bottom: 16px;
}

.pdf-main__plan {
  height: 280px;
}

.pdf-main__plan img {
  min-width: 200px;
  max-height: 100%;
}

.pdf-main__prices {
  margin-bottom: 16px;
}

.pdf-main__price {
  margin-bottom: 8px;
  font-size: 20px;
  line-height: 30px;
  font-weight: 700;
  color: var(--color-brand-dark);
}

.pdf-main__price_action_price {
  display: inline-block;
}

.pdf-main__action-wrapper {
  display: inline;
  font-size: 12px;
  line-height: 18px;
}

.pdf-main__action {
  display: inline-flex;
  align-items: center;
  position: relative;
  top: -3px;
  margin-left: 14px;
  padding: 0 6px;
  border-radius: 40px;
  background-color: rgba(var(--color-additional-1-rgb), 1);
  color: var(--color-white);
}

.pdf-main__action:before {
  content: "";
  position: absolute;
  width: 3px;
  height: 3px;
  left: -11px;
  display: block;
  top: 50%;
  border-radius: 50%;
  background-color: rgba(var(--color-brand-text-rgb), 0.2);
  transform: translateY(-50%);
}

.pdf-main__basic-price {
  text-decoration: line-through;
  color: rgba(var(--color-brand-text-rgb), 0.6);
}

.pdf-main__list {
  font-size: 14px;
  line-height: 21px;
}

.pdf-main__list-item {
  display: flex;
  align-items: center;
}

.pdf-main__list-type {
  color: rgba(var(--color-dark-rgb), 0.5);
}

.pdf-main__list_theme_dashed .pdf-main__list-item {
  justify-content: space-between;
  padding: 4px 0;
  border-bottom: 1px dashed rgba(var(--color-dark-rgb), 0.1);
}

.pdf-main__list_theme_dotted {
  display: flex;
  flex-wrap: wrap;
}

.pdf-main__list_theme_dotted .pdf-main__list-item {
  position: relative;
  margin-right: 12px;
  white-space: break-spaces;
}

.pdf-main__list_theme_dotted .pdf-main__list-item:not(:last-child):after {
  position: absolute;
  right: -8px;
  width: 3px;
  height: 3px;
  border-radius: 50%;
  background-color: rgba(var(--color-dark-rgb), 0.3);
  content: "";
}

.pdf-main__specials {
  display: flex;
  margin-top: 16px;
}

.pdf__info-building {
  font-size: 16px;
  line-height: 22px;
}

.pdf__params {
  margin-top: 24px;
  display: flex;
  font-size: 16px;
  line-height: 22px;
  margin-bottom: 48px;
}

.pdf__params-search {
  margin-top: 10px;
  display: flex;
  font-size: 16px;
  line-height: 22px;
  margin-bottom: 48px;
}

.pdf__params-prices {
  margin-right: 3.3333333333%;
  width: 23.3333333333%;
  font-weight: 700;
  font-size: 16px;
}

.pdf__prices {
  margin-right: 3.3333333333%;
  width: 23.3333333333%;
  font-weight: 700;
  font-size: 16px;
  margin-top: 24px;
}

.pdf__prices-commerce {
  max-width: 100%;
}

.pdf__prices-sales {
  margin-top: 5px;
  color: rgba(var(--color-black-rgb), 0.5);
}

.pdf__params-info {
  margin-right: 3.3333333333%;
  flex-grow: 1;
}

.pdf__params-options {
  width: 26.6666666667%;
}

.pdf__params-options-commerce {
  min-width: 270px;
}

.pdf-planoplan__plan {
  height: 210px;
}

.pdf-planoplan__plan img {
  min-width: 210px;
  max-height: 100%;
}

.pdf-planoplan__text {
  margin-top: 10px;
}

.pdf-planoplan__app {
  display: flex;
  margin-top: 22px;
}

.pdf-planoplan__qr {
  margin-right: 6.6666666667%;
  width: 26.6666666667%;
}

.pdf-planoplan__list {
  display: flex;
  flex-direction: column;
}

.pdf-planoplan__list li {
  height: 30px;
}

.pdf-planoplan__list li:not(:last-child) {
  margin-bottom: 8px;
}

.pdf-planoplan__list img {
  width: 102px;
  height: 30px;
}

.pdf-plan img {
  max-height: 160px;
  width: auto;
}

.pdf-plan__text {
  margin-top: 8px;
  color: rgba(var(--color-black-rgb), 0.5);
}

.pdf-about {
  margin-top: 32px;
}

.pdf-about__address {
  display: flex;
  align-items: center;
  margin-top: 16px;
}

.pdf-about__address svg {
  margin-right: 12px;
  fill: var(--color-dark);
}

.pdf-about__images {
  display: flex;
  align-items: flex-start;
  margin-top: 16px;
  margin-bottom: 32px;
  height: 200px;
}

.pdf-about__images-item {
  width: 51.6666666667%;
  border-radius: 8px;
  max-height: 100%;
}

.pdf-about__images-item:first-child {
  margin-right: 3.3333333333%;
}

.pdf-details {
  margin: 32px 0 16px;
}

.pdf-details__list {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
}

.pdf-details__list li {
  display: inline-block;
  width: 26.6666666667%;
  min-width: 26.6666666667%;
  margin-bottom: 16px;
}

.pdf-details__list li:not(:nth-child(3n)) {
  margin-right: 3.3333333333%;
}

.pdf-details__heading {
  color: rgba(var(--color-dark-rgb), 0.6);
}

.pdf-contacts {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  padding-top: 32px;
}

.pdf-contacts:before {
  position: absolute;
  top: 0;
  width: 100%;
  border-top: 1px solid rgba(var(--color-black-rgb), 0.1);
  content: "";
}

.pdf-contacts__list {
  margin-right: 16.6666666667%;
  width: 36.6666666667%;
  font-size: 14px;
  line-height: 21px;
}

.pdf-contacts__list:nth-child(even) {
  margin-right: 0%;
}

.pdf-contacts__list li:not(:last-child) {
  margin-bottom: 8px;
}

.pdf-contacts__list span {
  display: block;
}